:root {
  --border-rad-lg: 15px;
  --light-text: #fefefe;
}

*, *::before, *::after {
  box-sizing: border-box;  
}

html, body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins';
}

main {    
  background-color: slategrey;
  background-image: url('./assets/images/scrimba-bg.jpeg');
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* chatbot elements */

.chatbot-container {
  background-color: #171f26;
  width: 100%;
  height: 100%;
  border-radius: var(--border-rad-lg);
  padding: 1em;  
}

.chatbot-container > * {
  padding: .5em;   
}

.chatbot-header {
  display: flex;
  flex-direction: column;
  gap: .6em;
}

.logo {
  width: 160px;
}

.chatbot-conversation-container {
  height: 250px;
  overflow-y: scroll;
  margin: 1em 0;
}

/* stop ugly scroll bar on some browsers */
.chatbot-conversation-container::-webkit-scrollbar {
  display: none;
}

.chatbot-conversation-container::-moz-scrollbar {
  display: none;
}

.speech {
  padding: 1em;
  max-width: 240px; 
  color: var(--light-text);
  min-width: 90%;
  border-radius: var(--border-rad-lg); 
  font-size: 1.07em;
}

.speech:first-child {
  margin-top: 0;
}

.speech-ai {
background: #334959;
  border-top-left-radius: 0;
  margin: 1.2em 1em 0 0; 
}

.speech-human {
  margin: 1.2em 0 0 1em; 
background: #2f4f4f;
  border-top-right-radius: 0; 
}

/* chatbot elements */

.chatbot-container {
  background-color: #171f26;
  width: 95%;
  height: 95%;
  border-radius: var(--border-rad-lg);
  padding: 1em;
  display: flex;
  flex-direction: column;
}

.chatbot-input-container {
  display: flex;
  margin-top: auto;
}

input[type="text"], button {
  background-color: transparent;
  border: 1px solid #586e88;
  border-radius: var(--border-rad-lg);
  padding: 1em;
}

input[type="text"] {
  color: var(--light-text);
  width: 100%;
  border-right: 0; 
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

button {
  border-left: 0; 
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.send-btn-icon {
  width: 20px;
  display: block;
}

/* text */
.sub-heading {
  color: #999999;
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  text-transform: uppercase;
  margin: 0;
}